html{
	overflow: hidden;
}

.wrapper {
	display: grid;              /* Activate CSS Grid */
    grid-template-columns: 1fr 2fr; /* Create two columns (1:2 ratio) */
	justify-items: center;      /* Horizontally center the grid items */
    align-items: normal;
	/* margin-: 5%, 0, 5% , 5%; */
}

.container{
	background-color: #7c7c7c;
	position: absolute;
	right:0;
	left:0;
	height: 90%;
	/* opacity: 1; */
}
.flex{
	display: flex;
	flex-direction: column;
	height: 90%; /* Full viewport height */
	padding: 10%;
  }
  
  .top, .bottom {
	flex: 1; /* Each element takes up 50% of the available vertical space */
	display: flex;
	align-items: center; /* Vertically center the content */
	justify-content: center; /* Horizontally center the content */
  }

canvas {
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	position: absolute;
	z-index: -1;
}

.textBody{
	display: flex;              /* Use flexbox */
	align-items: center;        /* Vertically center the content */
	justify-content: center;    /* Optionally, horizontally center the content */
	text-align: center; 
	padding-left: 10%;
	height:100%;
}
.textAlign{
	text-align:center;
}
.textMono{
	font-family:monospace;
}
.textSans{
	font-family:Tahoma, sans-serif;
}
.padTop{
	padding-top:5%;
}
.padLeft{
	padding-left:5%;
}
.opacity-little{
	opacity: .3;
}
.opacity-lots{
	opacity:1;
}
.header {

	overflow: hidden;
	margin: auto;
	text-align: center;
	width: 100%;
	height: 10%;
	top:0;
	background-color: rgb(155, 147, 167);
	/* background-color: rgb(176, 176, 176); */
}
.headerText{
	color:white;
}

ul#horizontal-list {
	list-style: none;
}
ul#horizontal-list li {
	display: inline;
}
h1 {
	color: white;
}
p {
	font-size:large;
	font-family:monospace;
}
.padAll{
	padding: 10%;
}
.border1{
	border: 3px solid rgb(68, 89, 111);	
}
.border2{
	border: 3px solid rgb(22, 22, 23);
}
.rounded-box {
	border-radius: 12px;          /* Set the rounding of the corners */
	padding: 20px;
	margin: 10px;
  }
a, h2{
	color:inherit;
	text-decoration:none;
}